




import Vue from "vue";
import GoldenLayout from "golden-layout";
import { initPopOut } from "./GoldenLayoutHelper";
import { IComponentCollection } from "../../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IDomElement } from "../../../../../../common/shrimp/interfaces/RenderingInterfaces/IDomElement";
import { ContextManager } from "../../../../../../common/shrimp/context";
export default Vue.extend({
  name: "GoldenLayout",
  data() {
    return {
      config: {
        settings: { showPopoutIcon: false, selectionEnabled: true },
        dimensions: {},
        content: [
          {
            type: "stack",
            id: "default",
            content: [
              {
                type: "component",
                componentName: "component",
                title: "default",
                componentState: {
                  id: "default",
                  text: "default",
                  class: "default" + (Math.random() * 100000).toFixed(0),
                },
              },
            ],
          },
        ],
      },
      getGoldenLayout: <() => GoldenLayout>(<unknown>null),
      getContentItems: <() => Array<any>>(<unknown>null),
      getComponentInCreation: <() => IComponentCollection>(<unknown>null),
      componentCb: <() => IComponentCollection>(<unknown>null),
      StateChangedForDesign: false,
      closedPanes: [],
      containerWidth: 0,
      containerHeight: 0,
      childWindows: []
    };
  },
  props: {
    _renderingProps: {
      default: {
        context: undefined as unknown as () => ContextManager,
        isEditMode: undefined as unknown as boolean,
      },
    },
    width: {
      type: String,
      default: "1200px",
    },
    height: {
      type: String,
      default: "700px",
    },
    glConfig: {
      type: Object,
      default: null,
    },
    childNames: {
      type: Array,
      default: ["default"],
    },
    dimensions: {
      type: Object
    }
  },
  methods: {
    getClosedPanes() {
      return this.closedPanes;
    },
    getConfig() {
      return this.getGoldenLayout().toConfig();
    },
    setConfig(val: GoldenLayout.Config) {
      this.getGoldenLayout().config = val;
      this.getGoldenLayout().root.contentItems.forEach(item => {
        item.remove();
      });
      val.content?.forEach(item => {
        this.getGoldenLayout().root.addChild(item);
      });
      this.StateChangedForDesign = true;
    },
    findContentItem(paneName: string) {
      const self = this;
      let contentItems = self.getContentItems();
      let contentItem = contentItems.find(
        (item) => item.config.componentState.id == paneName
      );
      return contentItem;
    },
    changeTitle(childName: string, newTitle: string) {
      const self = this;
      let contentItem = self.findContentItem(childName);
      contentItem.setTitle(newTitle);
    },

    removePane(paneName: string) {
      const self = this;
      let pane = self.findContentItem(paneName);
      let childArray: Array<string> = [];
      childArray.push(paneName);
      self.removeItem(childArray);
      pane.remove();
      self.$emit("paneClosed", paneName);
    },

    //do not open or inform below methods to quick users.
    addItem({ childName, config }: { childName?: string; config?: GoldenLayout.ContentItem | GoldenLayout.ItemConfigType; }) {
      var newItem: GoldenLayout.ContentItem | GoldenLayout.ItemConfigType = {
        type: "component",
        componentName: "component",
        title: childName,
        componentState: {
          id: childName,
          text: childName,
          class: childName + (Math.random() * 100000).toFixed(0),
        },
      };

      if (config) {
        newItem = config;
      }
      var gl = this.getGoldenLayout();
      gl.root.contentItems[0].addChild(newItem);
      this.childNames.push(childName);
      var glConfig = gl.toConfig();
      this.$emit("paneCreated", childName);
      return glConfig;
    },
    goldenLayoutInit(stateChangedForDesign: boolean) {
      this.StateChangedForDesign = stateChangedForDesign;
    },
    removeItem(deletedChildNames: Array<string>) {
      const self = this;
      let dory: any = this._renderingProps.context().GetItemSingle("Dory");
      let historyItem = (<any>this._renderingProps.context().GetItemSingle("HistoryManager")).currentLevel().last();
      let pjson = historyItem.qjson.pJson.find((i: { T: string; }) => i.T == "GoldenLayout");

      deletedChildNames.forEach(childName => {
        if (self._renderingProps.isEditMode) {
          self.childNames.splice(self.childNames.indexOf(childName), 1);
        }
        if (pjson.C[childName] && !self._renderingProps.isEditMode) {
          (<any>self.closedPanes).push(childName);
        }
        self.componentCb().children?.[childName]?.map(child => child.compId).forEach(childNameToDelete => dory.DestroyComponentCollectionByID(childNameToDelete));
        // remove after 02.07.2022
        // let contentItems = self.getContentItems();
        // let contentItemIndex = contentItems.findIndex((item) => item.config.componentState.id == childName);
        // contentItems.splice(contentItemIndex, 1);
        self.$emit("paneClosed", childName);
      });

      self.$emit("GL_stateChanged", self.getGoldenLayout().toConfig(), self.childNames);

    },
    customChildAppender({ childName, createdComps }: { childName: string, createdComps: Array<IDomElement> }) {
      const index = this.closedPanes.findIndex(item => item == childName);
      if (index > -1) { this.closedPanes.splice(index, 1); }
      this.addItem({ childName });
    },
    appendDomElements(container: any, componentConfig: any) {
      if (componentConfig.fromWindow) {
        container.getElement()[0].innerHTML = '<div class="' + componentConfig.class + '"></div>';
        componentConfig.fromWindow = false;
      } else {
        if (!this.componentCb) {
          const componentCb = this.getComponentInCreation(); //renderingProps da  __domEl var mı bak.
          this.componentCb = () => {
            return componentCb;
          };
        }
        var domElements = this.componentCb().children?.[componentConfig.id]?.map((i) => i.domElement);
        if (!domElements) {
          return;
        }
        container.getElement()[0].innerHTML = '<div class="' + componentConfig.class + '"><div class="innerWindow"></div>';
        domElements!.forEach((domElement) => {
          if (domElement) {
            container.getElement()[0].getElementsByClassName("innerWindow")[0].append(domElement);
          }
        });
      }
    },
    onStackClose(stack: any) {
      const self = this;
      stack.header.controlsContainer.find(".lm_close").off("click").click(function () {
        let childNames: Array<string> = [];
        for (let index = 0; index < stack.config.content.length; index++) {
          const element = stack.config.content[index];
          const childName = element.componentState.id;
          childNames.push(childName);
        }
        self.removeItem(childNames); // to delete child jsons in goldenLayout before stack remove
        self.StateChangedForDesign = self._renderingProps.isEditMode ? false : true; // to save new goldenLayout config. after stack remove gl will emit layoutChanged event and GoldenLayoutTracker will be triggered.
        stack.remove();
      });
    },
    resizeObserver() {
      const self = this;
      const resizeObserver = new ResizeObserver(entries => {
        entries.forEach(entry => {
          if (self.containerWidth != entry.target.clientWidth || self.containerHeight != entry.target.clientHeight) {
            self.containerWidth = entry.target.clientWidth;
            self.containerHeight = entry.target.clientHeight;
            self.getGoldenLayout().updateSize();
          }
        });
      });
      resizeObserver.observe(<HTMLElement>this.$refs["layoutContainer"]);
    },
    // remove after 02.07.2022
    // addContentItem(contentItem) {
    //   let contentItems = this.getContentItems();
    //   let checkExistencialCrisis = contentItems.find(i => i.config.componentState.id == contentItem.config.componentState.id);
    //   if (!checkExistencialCrisis) {
    //     contentItems.push(contentItem);
    //   }
    // },
    closeChildWindows() {
      const self = this;
      if (self.childWindows) {
        self.childWindows.forEach((glWindow) => {
          if (!(glWindow as any).childWindow.closed) {
            (glWindow as any).childWindow.close();
          }
        });
      }
    },
  },
  mounted() {
    const self = this;
    // ensure that the following are only done in the mounted lifecycle.
    self.closedPanes = [];
    var parentContainer = <HTMLElement>this.$refs["layoutContainer"];
    (<any>window).parentContainer = parentContainer;
    if (!parentContainer) {
      return;
    }
    // create a new GL instance.
    if (this.glConfig) {
      this.config = this.glConfig;
    }
    this.config.dimensions = this.dimensions || this.config.dimensions;

    const context: any = this._renderingProps.context();
    const rc = context.GetItemSingle("RenderingContext");
    this.getComponentInCreation = () => rc.componentInCreation;

    var layout = new GoldenLayout(this.config, parentContainer);
    this.resizeObserver();

    layout.registerComponent("component", function (container: any, componentConfig: any) {
      if (componentConfig.fromWindow) {
        container.getElement()[0].innerHTML = '<div class="' + componentConfig.class + '"></div>';
        componentConfig.fromWindow = false;
      }
      else {
        if (!self.componentCb) {
          const componentCb = self.getComponentInCreation() //renderingProps da  __domEl var mı bak.
          self.componentCb = () => { return componentCb };
        }
        var domElements = self.componentCb().children?.[componentConfig.id]?.map((i) => i.domElement);
        if (!domElements) {
          return;
        }
        container.getElement()[0].innerHTML = '<div class="' + componentConfig.class + '"><div class="innerWindow"></div>';
        domElements!.forEach((domElement) => {
          if (domElement) {
            container.getElement()[0].getElementsByClassName("innerWindow")[0].append(domElement);
            container.getElement()[0].style.overflow = "overlay";
          }
        });
      }
    });

    // attach the state change listener
    layout.on("stateChanged", () => {
      if (self.StateChangedForDesign) {
        self.StateChangedForDesign = false;
      } else {
        self.$emit("GL_stateChanged", self.getGoldenLayout().toConfig(), self.childNames);
      }
    });

    layout.on("stackCreated", function (stack: any) {
      initPopOut.call(self, stack);
      self.onStackClose(stack);
    });

    layout.on("tabCreated", function (tab: any) {
      // self.addContentItem(tab.contentItem);
      self.$emit("paneCreated", tab.contentItem.config.componentState.text);
      tab.closeElement.off("click").click(function () {
        const childName = tab.contentItem.config.componentState.text;
        self.removeItem([childName]); // to delete child jsons in goldenLayout before tab remove.
        self.StateChangedForDesign = self._renderingProps.isEditMode ? false : true; // to save new goldenLayout config. after tab remove gl will emit layoutChanged event and GoldenLayoutTracker will be triggered.
        tab.contentItem.remove();
      });
    });
    this.getContentItems = () => {
      let contentItems: Array<any> = [];
      (<any>self.getGoldenLayout())._getAllContentItems?.().forEach((item) => {
        if (item.config.type == "component") {
          contentItems.push(item);
        }
      });
      return contentItems;
    };
    // initiatialize the layout
    layout.init();
    this.getGoldenLayout = () => layout;
    let head = document.getElementsByTagName("head");
    let isCssExist: boolean = false;
    for (let item of <any>head[0].children) {
      if (item.href && item.href.includes("goldenlayout-base")) {
        isCssExist = true;
      }
    }
    if (!isCssExist) {
      let glBaseCssTag = document.createElement("link");
      glBaseCssTag.href =
        "https://golden-layout.com/files/latest/css/goldenlayout-base.css";
      glBaseCssTag.rel = "stylesheet";
      glBaseCssTag.type = "text/css";
      let glLightCssTag = document.createElement("link");
      glLightCssTag.href =
        "https://golden-layout.com/files/latest/css/goldenlayout-light-theme.css";
      glLightCssTag.rel = "stylesheet";
      glLightCssTag.type = "text/css";
      head[0].appendChild(glBaseCssTag);
      head[0].appendChild(glLightCssTag);
    }
  },
  destroyed() {
    this.$el?.remove();
  },
});
